// 1.  Vue  Vuex
import Vue from 'vue'
import Vuex from 'vuex'
import book from './book/book.js'
import video from './video/video.js'
import cartoon from './cartoon/cartoon.js'
// 2.  Vuex  Vue 
Vue.use(Vuex)
// 3.  Store 
const store = new Vuex.Store({
  // store 
  modules: {
    book,
    video,
    cartoon
  },
})

// 4.  Store 
export default store